.Sidebar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 30%;
  background: black;
  transform: translateX(-100%);
  transition: 0.3s linear;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px;
  }

  &__contact {
    font-size: 20px;
    color: black;
    padding: 8px 14px;
    margin: 50px auto 0;
    display: block;
    border-radius: 5px;
    background: #f9ba05;
    border: 0;
    transition: all .2s ease;
    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }

    &:active {
      transform: scale(.96);
    }
  }

  &__link {
    font-size: 18px;
    text-decoration: none;
    text-align: center;
    color: white;
    padding: 8px;
    margin: 0 auto 20px;
    display: block;
    width: 80%;
    border: 1px solid transparent;

    &:hover {
      border-bottom-color: white;
    }

    &--active {
      color: #f9ba05;
      border-bottom-color: #f9ba05;
    }
  }
}

.Sidebar--active {
  transform: translateX(0);
}
